export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static extractClipNames(data, dataFromSecondUrl) {
    const arrayFromFirstUrl = data.items.map(clip => [
      clip.snippet.thumbnails.medium.url,
      clip.snippet.title,
      clip.snippet.description,
      clip.snippet.channelTitle,
      clip.snippet.publishedAt,
      clip.id.videoId,
    ]);
    const arrayFromSecondUrl = dataFromSecondUrl.items.map(
      clip => clip.statistics.viewCount,
    );
    let count = 0;
    for (let i = 0; i < arrayFromFirstUrl.length; i += 1) {
      arrayFromFirstUrl[i].push(arrayFromSecondUrl[count]);
      count += 1;
    }
    return arrayFromFirstUrl;
  }

  async extractClipId(data) {
    const { urlId } = this.state;

    const arrayId = data.items.map(id => id.id.videoId);

    const url = arrayId.join(',');

    const newUrl = `${urlId + url}&part=snippet,statistics`;

    const responce = await fetch(newUrl);
    const dataFromSecondUrl = await responce.json();

    return dataFromSecondUrl;
  }

  async getClipNames() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();
    const arrIdUrl = await this.extractClipId(data);

    return AppModel.extractClipNames(data, arrIdUrl);
  }
}
