const input = document.createElement('form');
input.innerHTML = `<input type='text' class='search-input' placeholder='
enter here the name of the video' autofocus>
  <button class='button' type='button'><i class="fas fa-search"></i></button>`;
input.className = 'search-form';
document.body.appendChild(input);

// connecting fontawesome icons
const fontAwesome = document.createElement('link');
fontAwesome.rel = 'stylesheet';
fontAwesome.setAttribute(
  'href',
  'https://use.fontawesome.com/releases/v5.8.2/css/all.css',
);
fontAwesome.setAttribute(
  'integrity',
  'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay',
);
fontAwesome.crossOrigin = 'anonymous';
document.head.appendChild(fontAwesome);
const font = document.createElement('link');
font.rel = 'stylesheet';
font.setAttribute(
  'href',
  'https://fonts.googleapis.com/css?family=Open+Sans&display=swap',
);
document.head.appendChild(font);

export default class AppView {
  constructor(clips) {
    this.clips = clips;
    this.pageNumber = 1;
  }

  render() {
    document.body.style.backgroundImage = 'none';
    if (document.querySelector('.clip-card') !== null) {
      const clipCard = document.querySelector('.grid-container');
      document.body.removeChild(clipCard);
      const sliderButton = document.querySelector('.slider-buttons');
      document.body.removeChild(sliderButton);
    }

    const inputValue = document.querySelector('.search-input');
    inputValue.value = '';

    // creating a container with cards

    const content = document.createElement('div');
    content.className = 'clip-card';
    content.innerHTML = this.clips
      .map(
        title => `<div class="content-card">
            <div class='video-link'>
                <a href='https://www.youtube.com/watch?v=${
  title[5]
}' target='blank' >${title[1]}</a>
            </div>
      <img src=${title[0]} class='video-img'></img>
      <p class='description'>${title[2]}</p>
      <p class='statistics'><i class="fas fa-user-check icons"></i>${
  title[3]
}</p>
      <p class='statistics'><i class="fas fa-clock icons"></i>${title[4].substring(
    0,
    10,
  )}</p>
      <p class='statistics'><i class="fas fa-eye icons"></i>${title[6]}</p>
      </div>`,
      )
      .join('');
    const gridContainer = document.createElement('div');
    gridContainer.className = 'grid-container';
    const main = document.createElement('div');
    main.className = 'main';
    main.appendChild(content);
    gridContainer.appendChild(main);
    document.body.appendChild(gridContainer);

    // creating a container with buttons
    const sliderButtons = document.createElement('div');
    let { pageNumber } = this;
    sliderButtons.innerHTML = `<button class='prev slider-button' type='button'><i class="fas fa-chevron-left"></i></button><p class='count-page'>1</p>
    <button class='next slider-button' type='button'><i class="fas fa-chevron-right"></i></button>`;
    sliderButtons.className = 'slider-buttons';

    document.body.appendChild(sliderButtons);
    const prevButton = document.querySelector('.prev');
    const nextButton = document.querySelector('.next');
    const width = window.innerWidth + window.innerWidth * 0.012;
    function setNumberPageNext() {
      const count = document.querySelector('.count-page');
      if (pageNumber !== 1) {
        pageNumber -= 1;
        count.innerHTML = `${pageNumber}`;
      }
    }
    function setNumberPagePrev() {
      const count = document.querySelector('.count-page');
      if (pageNumber !== 5) {
        pageNumber += 1;
        count.innerHTML = `${pageNumber}`;
      }
    }
    prevButton.addEventListener('click', (e) => {
      e.preventDefault();
      content.scrollBy(-width, 0);
      setNumberPageNext();
    });
    nextButton.addEventListener('click', (e) => {
      e.preventDefault();
      content.scrollBy(width, 0);
      setNumberPagePrev();
    });

    const slider = document.querySelector('.clip-card');
    function scrollPrev() {
      slider.scrollBy(-width, 0);
      setNumberPageNext();
    }
    function scrollNext() {
      slider.scrollBy(width, 0);
      setNumberPagePrev();
    }
    const threshold = 30;
    let tempCoordX;

    function pointerDown(e) {
      e.preventDefault();
      tempCoordX = e.clientX;
    }

    function pointerUp(e) {
      e.preventDefault();
      if (e.clientX - threshold > tempCoordX) {
        scrollPrev();
      }
      if (e.clientX + threshold < tempCoordX) {
        scrollNext();
      }
    }

    slider.addEventListener('pointerdown', pointerDown);
    slider.addEventListener('pointerup', pointerUp);
  }
}
