import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      url: '',
      urlId:
        'https://www.googleapis.com/youtube/v3/videos?key=AIzaSyDMdEOxTAP6RGKFOFG7u1NjEMKFY6McynA&id=',
    };
    this.event();
  }

  event() {
    const button = document.querySelector('form');
    button.addEventListener('click', () => this.madeUrl());
    document.body.addEventListener('keydown', (e) => {
      if (e.keyCode === 13) {
        e.preventDefault();
        this.madeUrl();
      }
    });
  }

  async madeUrl() {
    const input = document.querySelector('.search-input');

    let { value } = input;
    if (value !== '') {
      value = value.split(' ').join('+');
      this.state.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyDMdEOxTAP6RGKFOFG7u1NjEMKFY6McynA&type=video&part=snippet&maxResults=15&q=${value}`;

      const model = new AppModel(this.state);
      const data = await model.getClipNames();
      const view = new AppView(data);
      view.render();
    }
    value = '';
  }
}
